import React from "react";
import {
  Col,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Input,
  ModalHeader,
  ModalBody,
  Modal,
  Row,
} from "reactstrap";

const ModalDashboard = (props) => {
  const { toggleSuccess, success } = props;

  return (
    <div className="animated fadeIn">
      <Modal
        isOpen={success}
        toggle={toggleSuccess}
        className={"modal-lg " + props.className}
      >
        <ModalHeader toggle={toggleSuccess}></ModalHeader>
        <ModalBody>
          محتوا به صورت تستی بر روی گیت هاب قرار گرفته در صورت بروز هرگونه مشکل
          و برخورد با ارور 404 به لینک زیر مجدد مراجعه فرمائید یا از بخش نمونه کار ها مجددا کلیک نمائید تشکر
          <hr />
          <a href="https://rezasalehidev.github.io/adminpanel/">
            https://rezasalehidev.github.io/adminpanel/
          </a>
        </ModalBody>
      </Modal>
    </div>
  );
};

export default ModalDashboard;
